'use client';

import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { LogOut, ShieldCheck } from 'lucide-react';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { useAuth } from '@/hooks/use-auth';
import { Switch } from '@/components/ui/switch';
import { Label } from '@/components/ui/label';
import { SidebarTrigger, useSidebar } from '../ui/sidebar';
import { RotaScribeIcon } from '../icons/RotaScribeIcon';

export function Header() {
  const router = useRouter();
  const { isAdmin, logout, isLoggedIn, firebaseUser } = useAuth();
  const { isMobile } = useSidebar();


  const handleLogout = () => {
    logout();
  };
  
  if (!isLoggedIn) {
      return null;
  }

  const userInitial = firebaseUser?.displayName?.charAt(0) || (isAdmin ? 'A' : 'V');
  const userName = firebaseUser?.displayName || (isAdmin ? 'Admin User' : 'Volunteer User');
  const userEmail = firebaseUser?.email || (isAdmin ? 'admin@example.com' : 'volunteer@example.com');

  return (
    <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-14 items-center">
         {isMobile && <SidebarTrigger />}
        <Link href={isAdmin ? "/admin" : "/my-schedule"} className="mr-6 flex items-center space-x-2">
          <RotaScribeIcon className="h-6 w-6 text-primary" />
          <span className="font-bold font-headline sm:inline-block">RotaScribe</span>
        </Link>
        <div className="ml-auto">
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
               <Button variant="ghost" className="relative h-8 w-8 rounded-full">
                <Avatar className="h-8 w-8">
                  {firebaseUser?.photoURL && <AvatarImage src={firebaseUser.photoURL} alt={userName} />}
                  <AvatarFallback>{userInitial}</AvatarFallback>
                </Avatar>
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent className="w-56" align="end" forceMount>
               <DropdownMenuLabel className="font-normal">
                <div className="flex flex-col space-y-1">
                  <p className="text-sm font-medium leading-none">{userName}</p>
                  <p className="text-xs leading-none text-muted-foreground">
                    {userEmail}
                  </p>
                </div>
              </DropdownMenuLabel>
              <DropdownMenuSeparator />
              <DropdownMenuItem onClick={handleLogout}>
                <LogOut className="mr-2 h-4 w-4" />
                <span>Log out</span>
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </div>
    </header>
  );
}
