'use client';
import { useState, useEffect } from 'react';
import { Header } from '@/components/layout/Header';
import { Card, CardHeader, CardTitle, CardContent, CardDescription, CardFooter } from '@/components/ui/card';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { Button } from '@/components/ui/button';
import { Plus, Link as LinkIcon, Copy, Printer, Loader2, ArrowRight } from 'lucide-react';
import type { Volunteer } from '@/types';
import InviteVolunteerModal from '@/components/admin/InviteVolunteerModal';
import { Input } from '@/components/ui/input';
import QRCode from 'qrcode.react';
import { useToast } from '@/hooks/use-toast';
import Link from 'next/link';
import AppShell from '@/components/layout/AppShell';
import { collection, query, where } from 'firebase/firestore';
import { useAuth } from '@/hooks/use-auth';
import { useFirebase, useMemoFirebase } from '@/firebase';
import { useCollection } from '@/firebase/hooks/use-collection';


function VolunteerCard({ volunteer }: { volunteer: Volunteer }) {
  const initial = volunteer.name ? volunteer.name.charAt(0) : '?';
  return (
    <Card className="flex flex-col">
      <CardHeader className="flex flex-row items-center gap-4">
        <Avatar>
          <AvatarFallback>{initial}</AvatarFallback>
        </Avatar>
        <div className="grid gap-1">
          <CardTitle className="text-lg">{volunteer.name || 'Unnamed Volunteer'}</CardTitle>
           <CardDescription>{volunteer.email}</CardDescription>
        </div>
      </CardHeader>
      <CardFooter className="mt-auto">
        <Button asChild className="w-full">
            <Link href={`/admin/volunteers/${volunteer.id}`}>
                Manage Volunteer <ArrowRight className="ml-2 h-4 w-4" />
            </Link>
        </Button>
      </CardFooter>
    </Card>
  );
}

function SignupLinkCard() {
    const { churchId } = useAuth();
    const { toast } = useToast();
    const [signupUrl, setSignupUrl] = useState('');

    useEffect(() => {
        if (typeof window !== 'undefined' && churchId) {
            setSignupUrl(`${window.location.origin}/signup/${churchId}`);
        }
    }, [churchId]);
    
    const copyToClipboard = () => {
        if (!signupUrl) return;
        navigator.clipboard.writeText(signupUrl);
        toast({ title: "Copied!", description: "Signup link copied to clipboard." });
    }

    return (
        <Card className="col-span-1 md:col-span-2 lg:col-span-3">
            <CardHeader>
                <CardTitle>Volunteer Signup Link</CardTitle>
                <CardDescription>Share this link or QR code to allow volunteers to sign up themselves.</CardDescription>
            </CardHeader>
            <CardContent className="flex flex-col sm:flex-row items-center gap-6">
                <div className="flex-1 w-full space-y-2">
                   <div className="flex gap-2">
                     <Input readOnly value={signupUrl} className="bg-muted" />
                     <Button variant="outline" size="icon" onClick={copyToClipboard} disabled={!signupUrl}>
                        <Copy className="h-4 w-4" />
                     </Button>
                   </div>
                   <p className="text-xs text-muted-foreground">
                       Anyone with this link can join your volunteer roster.
                   </p>
                </div>
                 <div className="p-4 bg-white rounded-md border">
                    {signupUrl ? <QRCode value={signupUrl} size={128} /> : <div className="h-32 w-32 flex items-center justify-center bg-muted rounded-md"><Loader2 className="h-8 w-8 animate-spin text-muted-foreground" /></div>}
                </div>
            </CardContent>
             <CardFooter className="flex-wrap gap-2">
                {signupUrl ? (
                    <Button asChild variant="secondary">
                        <Link href={signupUrl} target="_blank">
                            Preview Signup Page <LinkIcon className="ml-2 h-4 w-4" />
                        </Link>
                    </Button>
                ) : (
                    <Button variant="secondary" disabled>
                        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        Loading Link...
                    </Button>
                )}
                 <Button asChild>
                    <Link href="/admin/volunteers/flyer" target="_blank">
                        Print Flyer <Printer className="ml-2 h-4 w-4" />
                    </Link>
                </Button>
            </CardFooter>
        </Card>
    )
}

export default function VolunteersPage() {
  const { churchId, isUserLoading } = useAuth();
  const { firestore } = useFirebase();
  const [isInviteModalOpen, setInviteModalOpen] = useState(false);

  const volunteersQuery = useMemoFirebase(() =>
    firestore && churchId ? query(collection(firestore, 'users'), where('churchId', '==', churchId)) : null,
    [firestore, churchId]
  );
  const { data: volunteers = [], isLoading: isLoadingVolunteers } = useCollection<Volunteer>(volunteersQuery);

  const isLoading = isUserLoading || isLoadingVolunteers;

  if (isLoading) {
    return (
        <div className="flex min-h-screen w-full flex-col items-center justify-center bg-background">
            <Loader2 className="h-8 w-8 animate-spin text-primary" />
            <p className="text-muted-foreground mt-2">Loading Volunteers...</p>
        </div>
    );
  }

  return (
    <AppShell>
      <div className="flex min-h-screen w-full flex-col bg-muted/40">
        <Header />
        <main className="flex-1 p-4 md:p-8">
          <div className="max-w-5xl mx-auto">
            <div className="flex items-center justify-between mb-6">
              <h1 className="text-3xl font-bold font-headline tracking-tight">
                Volunteers
              </h1>
              <Button onClick={() => setInviteModalOpen(true)}>
                <Plus className="mr-2 h-4 w-4" /> Invite Volunteer
              </Button>
            </div>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
              <SignupLinkCard />
              {(volunteers || []).map(volunteer => (
                <VolunteerCard key={volunteer.id} volunteer={volunteer} />
              ))}
            </div>
          </div>
        </main>
        <InviteVolunteerModal
          isOpen={isInviteModalOpen}
          onClose={() => setInviteModalOpen(false)}
        />
      </div>
    </AppShell>
  );
}
