
'use client';
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from '@/components/ui/dialog';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { Loader2, Send } from 'lucide-react';
import { sendEmail } from '@/lib/email';
import { useAuth } from '@/hooks/use-auth';

const inviteFormSchema = z.object({
  email: z.string().email({ message: 'Please enter a valid email address.' }),
});

type InviteFormValues = z.infer<typeof inviteFormSchema>;

interface InviteVolunteerModalProps {
  isOpen: boolean;
  onClose: () => void;
}

export default function InviteVolunteerModal({ isOpen, onClose }: InviteVolunteerModalProps) {
  const { toast } = useToast();
  const { churchId } = useAuth();
  const [isSubmitting, setIsSubmitting] = useState(false);

  const form = useForm<InviteFormValues>({
    resolver: zodResolver(inviteFormSchema),
    defaultValues: {
      email: '',
    }
  });

  const onSubmit = async (data: InviteFormValues) => {
    setIsSubmitting(true);
    try {
      const signupUrl = `${window.location.origin}/signup/${churchId}`;
      const emailSubject = "You're invited to join the volunteer team!";
      const emailBody = `
        <h1>Invitation from RotaScribe</h1>
        <p>You have been invited to join the volunteer team.</p>
        <p>Please click the link below to sign up and set your preferences:</p>
        <a href="${signupUrl}">Sign Up Now</a>
        <p>We look forward to serving with you!</p>
      `;

      await sendEmail({
        to: data.email,
        subject: emailSubject,
        htmlBody: emailBody,
      });

      // TODO: This should be handled by a Cloud Function trigger for accuracy.
      // This is a client-side approximation.
      // await incrementSummaryCount('volunteers', churchId);

      toast({
        title: 'Invitation Sent',
        description: `An invitation has been sent to ${data.email}.`,
      });
      form.reset();
      onClose();
    } catch (error: any) {
      console.error(error);
      toast({
        variant: 'destructive',
        title: 'Error Sending Invite',
        description: error.message || 'Failed to send invitation. Please try again.',
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle className="font-headline">Invite a New Volunteer</DialogTitle>
          <DialogDescription>
            Enter the email address of the person you want to invite.
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4 py-4">
            <FormField
              control={form.control}
              name="email"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Email</FormLabel>
                  <FormControl>
                    <Input placeholder="volunteer@example.com" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <DialogFooter>
              <Button type="button" variant="ghost" onClick={onClose} disabled={isSubmitting}>Cancel</Button>
              <Button type="submit" disabled={isSubmitting}>
                {isSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Send className="mr-2 h-4 w-4" />}
                Send Invite
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
